import React, {useState} from 'react'

function Card() {
    const [hovered, setHovered] = useState(false)
    
    function hov() {
        return hovered ? 'hoving' : 'nothoving'       
    }
    
    return (
        <div 
            className={`card ${hov()}`}
            onMouseEnter={() => setHovered(true)} 
            onMouseLeave={() => setHovered(false)}
        >
            <span>
                <img src="https://www.hot-dog.org/sites/default/files/gallery/regional/regional-B8-Steamed-Hot-Dog-Naked-HR.jpg" alt='hot dog'/>
            </span>
            <div className="content">
                <p className="date">December 18, 2023</p>
                <h3>How to Heat Up a Hot Dog</h3>
                <p className="desc">First things first: nobody wants a cold hot dog. Arguably, it's not even technically a hot dog in that state. Here's how to toast that thing up.</p>
            </div>
        </div>
    )
}

export default Card